import React from 'react';
import './App.css';
import Product from './Product';
import request from 'superagent';
import { Grid, Button } from '@material-ui/core';
import Login from './Login';
import { serverBase } from './config';

function App() {

  const [ products, setProducts ] = React.useState([]);
  const [ categories, setCategories ] = React.useState([]);

  const [ current, setCurrent ] = React.useState('');

  React.useEffect(() => {
    
    let filter = '';
    if(current){
      filter += '?category.id=' + current;
    }
    request.get(serverBase + '/products' + filter)
      .then((response) => {
        console.log('productos');
        setProducts(response.body);
      });

  }, [current]);

  React.useEffect(() => {
    request.get(serverBase + '/categories')
      .then((response) => {
        console.log('categorias');
        response.body.push({
          id: '',
          name: 'Todos',
        });
        setCategories(response.body);
      });
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('__jwt');
    window.location.href = '/';
  }

  if(!localStorage.getItem('__jwt')){
    return <Login />;
  }
  

  return (
    <div className="App">
      {categories.map((category) => {
        return <Button key={category.id}
          variant={current === category.id ? 'contained' : 'text'}
          color="primary"
          onClick={() => {
            setCurrent(category.id)
          }}>{category.name}</Button>
      })}

      <Button color="secondary" onClick={handleLogout} style={{ float: 'right' }}>
        Cerrar Sesi칩n
      </Button>

      <Grid container spacing={2}>
        {products.map((product) => {
          if(product.images.length === 0) return null;
          
          return <Grid item xs={4} key={product.id}>
            <Product name={product.name} id={product.id}
              image={serverBase + product.images[0].url}></Product>
          </Grid>
        })}
      </Grid>
    </div>
  );
}

export default App;
