import * as React from 'react';
import { Grid, Paper, TextField, Button } from '@material-ui/core';
import './Login.css';
import request from 'superagent';
import { serverBase } from './config';

const Login = () => {

    const handleSubmit = (event) => {
        let user = null;
        event.preventDefault();
        request.post(serverBase + '/auth/local')
            .send({
                identifier: event.target.email.value,
                password: event.target.password.value
            })
            .then((response) => {
                localStorage.setItem('__jwt', response.body.jwt);
                localStorage.setItem('__user', response.body.user.id);
                
                user = response.body.user;
                return request.get(serverBase + '/carts?user.id='+ response.body.user.id)
                    .set('Authorization', 'Bearer ' + response.body.jwt)
            })
            .then((response) => {
                if(response.body.length === 0){
                    request.post(serverBase + '/carts')
                        .set('Authorization', 'Bearer ' + localStorage.getItem('__jwt'))
                        .send({
                            user: user,
                            products: '[]'
                        })
                        .then(() => {
                            window.location.href = '/';
                        });
                } else {
                    window.location.href = '/';
                }
            })
    }

    return <Grid container justify="center">
        <Grid item xs={6}>
            <Paper className="login-form">
                <h1>Iniciar Sesi칩n</h1>
                <form onSubmit={handleSubmit}>
                    <TextField
                        id="email"
                        label="Correo Electr칩nico"
                        fullWidth
                        margin="normal"
                        />
                    <TextField
                        id="password"
                        label="Contrase침a"
                        type="password"
                        fullWidth
                        margin="normal"
                        />
                    <Button color="primary" variant="contained" type="submit">Entrar</Button>
                </form>
            </Paper>
        </Grid>
    </Grid>;
}

export default Login;