import * as React from 'react';
import { Paper, Button } from '@material-ui/core';
import request from 'superagent';
import { serverBase } from './config';

const Product = ({ name, image, id }) => {
    const handleAdd = (productId) => {
        request.get(serverBase + '/carts')
            .set('Authorization', 'Bearer ' + localStorage.getItem('__jwt'))
            .then((response) => {
                console.log(response);
                var cart = response.body[0];
                var index = cart.products.findIndex((p) => {
                    return p.id === productId;
                });
                if(index < 0){
                    cart.products.push({
                        quantity: 1,
                        product_id: productId
                    });
                } else {
                    cart.products[index].quantity++;
                }
                console.log(cart)
                request.put(serverBase + '/carts/' + cart.id)
                    .set('Authorization', 'Bearer ' + localStorage.getItem('__jwt'))
                    .set('Content-Type', 'application/json')
                    .send({
                        products: { test: 'hola' }
                    })
                    .then(console.log);
            })
    }

    return <Paper>
        <img width="100px" src={image} alt=""/>
        {name}
        <Button variant="outlined" color="primary"
            onClick={() => handleAdd(id)}>Agregar</Button>
    </Paper>;
}

export default Product;